.infos-pokemon{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pokemon-img{
    width: 300px;
}

.pokemon-types{
    display: flex;
}

.infos-pokemon h1{
    text-transform: capitalize;
    margin-top: 30px;
    margin-bottom: 70px;
}

.pokemon-size{
    width: 300px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 50px;
}

.pokemon-type{
    list-style: none;
    text-align: start;
    margin-left: 10px;
}