.card{
    width: auto;
    height: auto;
    margin: 0px 5px 15px 10px;
    background-color: #0000003a;
    border-radius: 2px;
    border: 1px solid black;
    cursor: pointer;
}

a{
    text-decoration: none;
    color: #222;
}

.card h4{
    margin-top: 5px;
}

.card:hover{
    transform: scale(1.1);
}